import React from "react";
import { Button } from "react-bootstrap";

const UpvotesSection = ({ articleName, setArticleInfo, upvotes }) => {
  const upvoteArticle = async () => {
    const result = await fetch(`/api/articles/${articleName}/upvote`, {
      method: "POST",
    });
    const body = await result.json();
    setArticleInfo(body);
  };
  const downvoteArticle = async () => {
    const result = await fetch(`/api/articles/${articleName}/downvote`, {
      method: "POST",
    });
    const body = await result.json();
    setArticleInfo(body);
  };

  return (
    <>
      <p>This Post has been upvoted {upvotes} Times</p>
      <Button variant="success" onClick={() => upvoteArticle()}>
        +
      </Button>
      <Button variant="danger" onClick={() => downvoteArticle()}>
        -
      </Button>
    </>
  );
};

export default UpvotesSection;
